import React from "react";
import css from "./dev.module.css";
import gamejam from "./assets/clecio_game_jam-min.png";

export const Development = () => {
    return(
        <article className={css.dev}>
            <h2>Nascido de uma game jam</h2>
            <div className={css.jamSection}>
                <div>
                    <p><i>Komodora</i> foi criado em sete dias para a <i>ClcoGameDev Jam #2</i>. O tema era <i>DRAGO</i>, e para fugir do bvio (um animal mstico e castelos medievais), nos veio a ideia de um jogo sobre esses <i>animais nicrveis</i> que habitam a ilha de <i>Komodo</i>, na <i>Indonsia</i>.</p>
                    <p>O jogo foi implementado na <i>Godot Engine</i>. As artes foram produzidas usando o <i>LibreSprite</i> e os sons foram mixados no <i>Audacity</i>.</p>
                    <p>O ideia deu muito certo, e Komodora foi <i>campeo</i> da GameJam. Aps o trmino da jam, decidimos continuar o projeto e nossa inteno  criar uma <i>verso comercial</i> do jogo.</p>
                </div>
                <img src={gamejam} title="ClecioGameDev Jam #2"/>
            </div>
        </article>
    )
}